.githublink{
    position: absolute;
    top: 10px;
    background-color: green;
    border-radius: 2px;
    padding: 4px;
    color: white;
    -webkit-transition-duration: .5s;
            transition-duration: .5s;
}
.githublink a{
    text-decoration: inherit;
    color: white;
}
    .githublink:hover{
        position: absolute;
        top: 10px;
        background-color: green;
        opacity: 50%;
        -webkit-transition-duration: .5s;
                transition-duration: .5s;
        border-radius: 2px;
        padding: 4px;
        color: white;
    }
.App {
    box-sizing: border-box;
